import React,{useEffect, useState} from 'react';
import './Cube.scss'
import Cube from 'react-3d-cube';

import gif from '../../assets/images/gif.gif'


function LogoCube(){
    const[width,setWidth]=useState('')
    useEffect(()=>{
        // setTimeout(()=>{
        //     document.getElementsByClassName('_cube_bssuh_1')[0].style.transform='translate3d(0px, 0px, -152px) rotateX(1469deg) rotateY(-410deg) rotateZ(0deg)'

        // },[2000])
        if(window.innerWidth<600){
            setWidth(294)
        }
        else{
            setWidth(354)
        }
        window.addEventListener('resize',()=>{
            if(window.innerWidth<600){
                setWidth(294)
                console.log('working')
            }
            else{
                setWidth(354)
            }
        })
    },[])
    return (
        <div  className='cube-container'>
            <div className='cube-wrapper'>
                {Number(width)&& <Cube size={width} index="back">
                    <div>
                        <img style={{height:`${width-4}px`,width:`${width-4}px`}}src={gif}></img>
                    </div>
                    <div>
                        <img style={{height:`${width-4}px`,width:`${width-4}px`}}src={gif}></img>
                    </div>
                    <div>
                        <img style={{height:`${width-4}px`,width:`${width-4}px`}}src={gif}></img>
                    </div>
                    <div>
                        <img style={{height:`${width-4}px`,width:`${width-4}px`}}src={gif}></img>
                    </div>
                    <div>
                        <img style={{height:`${width-4}px`,width:`${width-4}px`}}src={gif}></img>
                    </div>
                    <div>
                        <img style={{height:`${width-4}px`,width:`${width-4}px`}}src={gif}></img>
                    </div>
                </Cube>}
               
            </div>
        </div>
    );
  
}
export default LogoCube