import './App.scss';
import { ToastContainer } from 'react-toastify';

import "react-toastify/dist/ReactToastify.css";

import MintSection from './components/mint-section/MintSection'
import { useEffect, useState } from 'react';

import Bottom from './components/bottom/Bottom'

import Cube from './components/cube2/Cube';



function App(){
 
 
  const[days,setDays]=useState('')
  const[hours,setHours]=useState('')
  const[minutes,setMinutes]=useState('')
  const[seconds,setSeconds]=useState('')
  const[timerFinished,setTimerFinished]=useState(false)
  const[visible,setVisible]=useState(false)
  // Update the count down every 1 second
  useEffect(()=>{
    var countDownDate = new Date(Date.UTC(2022,3,16,1)).getTime();

    var timer = setInterval(function() {
  
      // Get today's date and time
      var now = new Date().getTime();
     
      // Find the distance between now and the count down date
      var distance = countDownDate - now;
    
      // Time calculations for days, hours, minutes and seconds
      var days = Math.floor(distance / (1000 * 60 * 60 * 24));
      var hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
      var minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
      var seconds = Math.floor((distance % (1000 * 60)) / 1000);
      setVisible(true)

      if(seconds>9){
        setSeconds(`${seconds}`)
      }
      else{
        setSeconds(`0${seconds}`)
      }
      if(minutes>9){
        setMinutes(`${minutes}`)
      }
      else{
        setMinutes(`0${minutes}`)
      }
      if(hours>9){
        setHours(`${hours}`)
      }
      else{
        setHours(`0${hours}`)
      }
      if(days>9){
        setDays(`${days}`)
      }
      else{
        setDays(`0${days}`)
      }
  
      if (distance < 0) {
        clearInterval(timer);
        setTimerFinished(true)
      }

    }, 1000);
  
  },[])

  return (
    <div className="App">
      <ToastContainer style={{top:'75px'}}  position="top-right" autoClose={5000} closeOnClick />
      <div className='text-center font-l text-white weight-bold mt-4'>
        LUCKY APE TRAVEL CLUB PRESALE
      </div>
      {/* <div className='congs-text'> */}

        <p  className='congs-text text-center font-xs text-white mt-1 weight-light'>
          Congratulations, you are one of the lucky few who made it to our WhiteList!
          You are able to mint up to 2 NFTs at the price of .777 ETH.
        </p>

      {/* </div> */}


      {/* {days!==''&& */}
        <div style={{opacity:hours===''?'0':'1'}} className='timer-div font-time text-white weight-semi-bold'>
          <div className='timer-div-child'>
            <div>
              {days} 
            </div>
            <div className='font-s'>DAYS</div>
            
          </div>
          <div className='timer-div-child'>
            <div>
              {hours} 
            </div>
            <div className='font-s'>HOURS</div>

          </div>
          <div className='timer-div-child'>
            <div>
              {minutes} 
            </div>
            <div className='font-s'>MINUTES</div>

          </div>
          <div className='timer-div-child'>
            <div>{seconds} </div>
            <div className='font-s'>SECONDS</div>

          </div>
        </div>
      {/* } */}
      

      <MintSection/>
     
      {/* <AccountsData/> */}
      {/* <Cube/> */}
      <Bottom/>
      <div className='footer'>
        <p className='font-xs text-white weight-light'>
          Copyright © 2022 Lucky Ape Travel Club™ ®. Clover Lab inc. | All Rights Reserved
        </p>
      </div>
    </div>
  );
}

export default App;
